body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#page {
  background-image: linear-gradient(to bottom, #E674A2, #49AD9A);
  height: 100%;
  min-height: 100vh;
  padding-top: 30px;
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  padding-bottom: 30px;
  font-weight: 400; }

#app-container {
  max-width: 500px;
  margin: auto;
  background-color: white;
  padding: 45px;
  padding-top: 50px;
  min-width: 400px;
  min-height: 550px; }

button {
  display: inline-block;
  border: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
  background: none;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center; }

button:hover,
button:focus {
  background: none; }

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px; }

button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99); }

